// import all_tlds from "universal-wordsalad-scripts/data/domains/object.js";
import list_from_chunks from 'universal-wordsalad-scripts/suggestions/lists_from_chunks';
import { data_domains_availability } from 'src/redux/actions/api/data_domain';

/*
 * results
 */
export function RX__results_toggle_list(listType = '') {
  if (listType) {
    return {
      type: 'RX__results_toggle_list',
      listType: listType
    };
  }
}

/*
 * tlds
 */
export function RX__tld_add(string = '', list = 'unchecked') {
  if (list.substr(-2) === 'ed') {
    list = list.substring(0, list.length - 2);
  }
  return {
    type: 'RX__tld_' + list,
    tld: string
  };
}

/*
 * suggestions
 */
export function RX__domain_suggestions() {
  return async (dispatch, getState) => {
    let { word, domain } = getState();

    /*
     * Request
     */
    let suggestions = {};
    if (word.chunks.length) {
      /*
       * Get suggestions from BE (for now, it is actually on the FE)
       */
      suggestions = list_from_chunks(
        word.chunks,
        Object.keys(domain.tlds_user),
        Object.keys(domain.tlds_checked),
        Object.keys(domain.tlds_unchecked)
      );
    }

    /*
     * Response
     */
    dispatch({
      type: 'RX__domain_suggestions',
      suggestions: suggestions
    });
    dispatch({
      type: 'RX__tlds_checked',
      tlds: suggestions.tlds
    });
  };
}

export function RX__domain_suggestions_availability() {
  return async (dispatch, getState) => {
    let { domain } = getState();
    let { suggestions_availability, suggestions_all } = domain;
    /*
     * Suggestions
     */
    let suggestions = suggestions_all || [];
    // remove spaces
    suggestions = suggestions.map(word => word.replace(/ /g, ''));
    // keep only ones which don't already have availability score
    suggestions = suggestions.filter(word => {
      return !suggestions_availability[word] || suggestions_availability[word] === "0";
    });
    /*
     * Request availability of suggestions
     */
    return dispatch(data_domains_availability(suggestions))
      .then((response) => {
        if (response && typeof response.data === 'object') {
          // save
          dispatch({
            type: 'RX__domain_suggestions_availability',
            data: response.data
          });
        }
      });
  };
}