class AccountMenu extends React.Component {
  render() {
    // url search term
    let word = this.props.word_input;
    let url = word ? '/' + word : '';
    // view
    return (
      <Menu className={'MenuDropdownHeaderRight'}>

        {/*
         * Nav dropdown
         */}
        <Menu.Item>
          <Link to={"/word" + url}>
            <h6>synonym / word</h6>
          </Link>
        </Menu.Item>
        <Menu.Item className="selected">
          <Link to={"/domain" + url}>
            <h6>domain name</h6>
          </Link>
        </Menu.Item>
        <Menu.Item>
          <Link to="/">
            <h6>app store availability</h6>
          </Link>
        </Menu.Item>
        <Menu.Item>
          <Link to="/">
            <h6>social media handle</h6>
          </Link>
        </Menu.Item>
        <Menu.Item>
          <Link to="/">
            <h6>event</h6>
          </Link>
        </Menu.Item>
        <Menu.Item>
          <Link to="/">
            <h6>tech job</h6>
          </Link>
        </Menu.Item>
        <Menu.Item className="subdued">
          <Link to={"/edit_word" + url}>
            <h6>edit word</h6>
          </Link>
        </Menu.Item>
        <Menu.Item className="subdued">
          <Link to={"/edit_domain" + url}>
            <h6>edit domain</h6>
          </Link>
        </Menu.Item>
      </Menu>
    );
  }
}

