      {/*<div className={'heading'}>*/}
      {/*	<div className="title">Extensions:</div>*/}
      {/*</div>*/}

      {/*
       * Buttons
       */}
      {/*<Button*/}
      {/*	className="optionsButton"*/}
      {/*	shape="round"*/}
      {/*	onClick={() => {*/}
      {/*	}}*/}
      {/*>*/}
      {/*	<i className={'fas fa-sliders-h'}></i>*/}
      {/*	<span>&nbsp;More Filters</span>*/}
      {/*</Button>*/}

      {/*<Button*/}
      {/*	className="Button"*/}
      {/*	onClick={() => {*/}
      {/*	}}*/}
      {/*>*/}
      {/*	<i className={'fas fa-plus'}></i>*/}
