export const ColorsStyled = styled.div`
  // default "status0" while gettingAvailability
  // will be replaced with a code after gotAvailability
  .domname.status0,
  .domname.status0 .ant-checkbox {
    color: var(--color-medium);
  }
  // default text
  .domname {
    color: hsl(40, 55%, 70%);
  }
  // available
  .domname.status1,
  .domname.status1 .ant-checkbox {
    color: hsl(120, 75%, 33%);
  }
  // premium
  .domname.status2,
  .domname.status2 .ant-checkbox {
    color: var(--color-accent);
  }
  // unknown
  .domname.status9,
  .domname.status9 .ant-checkbox {
    color: hsl(40, 55%, 70%);
  }
  // unavailable
  .domname.status10,
  .domname.status10 .ant-checkbox {
    color: hsl(0, 0%, 70%);
  }
  
  /*
   * domain suggestions (while getting availability, until received results)
   */
  &.gotAvailability {
    // fix color of "unknown 0" to match "other" (THE REAL DEFAULT COLOR)
    .domname.status0 {
      color: hsl(0, 0%, 25%);
    }
    // hide available
    &.hideAvailable {
      .domname.status1 {
        display: none;
      }
    }
    // hide unavailable
    &.hideUnavailable {
      .domname.status10 {
        display: none;
      }
    }
    // hide premium
    &.hidePremium {
      .domname.status2 {
        display: none;
      }
    }
    // hide unknown
    &.hideOther {
      // hide all except already colored ones
      .domname:not(.status1):not(.status2):not(.status3):not(.status4):not(.status10) {
        display: none;
      }
    }
  }
`;
