import React from 'react';
import { Link } from 'react-router-dom';
import Field from './Field';
import Pos from './Pos';
import FieldList from './FieldList';
import { Styled } from './ViewWord.styled';

export default function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that;
  let { word_input, word_chunks, api_actions } = that.props;
  if (word_input && (!word_chunks.length || !word_chunks[0].length || !word_chunks[0][0].key)) {
    return (
      <Styled className="ViewWord content">
        <p>Word not found</p>
      </Styled>
    );
  }
  let row = word_chunks[1] ? word_chunks[1][0] : word_chunks[0][0];

  return (
    <Styled className="ViewWord content">
				{/*Edit*/}
      <Link className={'editWordLink'} to={`/edit_word/${row.key}`}>
					<i className={'fas fa-edit'}></i>
				</Link>
      {/*Fields*/}
      <div className={'ui-form-section ui-form-fieldset-grid'}>
					{[
            'key',
            'root',
            'singular',
            'plural',
            'abbreviation',
            'acronym',
            'list_count',
            'agg_sentiment',
            'ws_sentiment'
          ].map((field, pi) => (
            <Field field={field} row={row} key={row.key + pi}/>
          ))}
				</div>
      {/*Lists*/}
      <div className={'ui-form-section'}>
					{[row.pos1, row.pos2, 'before', 'after', 'etymology', 'etc'].map((pos, pi) => (
            <Pos api_actions={api_actions} pos={pos} row={row} key={pos + pi} expand={false}/>
          ))}
        {['ok_list', 'list', 'derivations'].map((field, pi) => (
          <FieldList field={field} row={row} key={row.key + pi}/>
        ))}
				</div>
			</Styled>
  );
}
