import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { withRouter } from 'react-router-dom';
import api_actions from "src/redux/actions/api";
import { StyledPage, StyledSearch } from "../../../Layout.styled";
import Search from "../../../components/Search";
import WordPoss from "../../../components/WordPoss";
import EditWordResults from "./EditWordResults";


class ViewWord extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    window.doneLoading('render');
    let PageContent = null;
    let { word_chunks, word_input } = this.props;
    /*
     * Verify Content:
     */
    if (!word_input || !word_chunks.length) {
      PageContent = <p className="content"></p>;
    } else if (!word_chunks.length || !word_chunks[0].length || !word_chunks[0][0].key) {
      PageContent = <p className="content">Word or phrase you entered is not found &#9757;</p>;
    } else {
      PageContent = <EditWordResults that={this}/>;
    }
    /*
     * Render content:
     */
    return (
      <>
				<StyledSearch>
					<Search
            {...this.props}
            domains={false}
            cue={[
              <span key="1">Edit the thesaurus database. &#9757;&nbsp;</span>,
              <span key="2">(Adding new words/phrases is currently disabled.)</span>,
            ]}
          />
					<WordPoss {...this.props} />
				</StyledSearch>

				<StyledPage>{PageContent}</StyledPage>
			</>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    api_actions: bindActionCreators(api_actions, dispatch),
  };
};


const mapStateToProps = function (state) {
  return {
    word_input: state.word.input,
    word_chunks: state.word.chunks,
  }
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(ViewWord));


