import React from 'react';
import { DomainsResultsStyled, ShowMoreStyled, ColorsStyled } from './Domains.styled';
import { Button, Checkbox } from 'antd';
import { asort_by_length_asc } from 'universal-common-scripts/functions/asort_strings';
import { Link } from 'react-router-dom';
import MoreOptions from './MoreOptions';
import Tlds from './Tlds';
import { Styled } from "./MoreOptions.styled";

const DomainsResults = function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that;
  /*
   * Same as this.render():
   */
  let {
    suggestions_all,
    suggestions_wip,
    suggestions_wip_phrases,
    suggestions_lists,
    suggestions_availability,
  } = that.props;

  if (!suggestions_all || !suggestions_all.length) {
    return (
      <DomainsResultsStyled>
        <div className="content">
          <p>No results...</p>
        </div>
      </DomainsResultsStyled>
    );
  }

  return (
    <>
      {/*
       * Options
       */}
      <MoreOptions that={that}/>

      {/*
       * BODY / CONTENT / RESULTS
       */}
      <DomainsResultsStyled>
        <div className="content">

          {/*
           * Show more...
           */}
          <ShowMoreStyled onClick={that.toggleWip}>
            <i className="fas fa-ellipsis-h"></i>
          </ShowMoreStyled>

          {/*
           * Columns
           */}
          <ColorsStyled
            className={
              'columns' +
              (!that.state.gotAvailability ? ' gettingAvailability' : ' gotAvailability') +
              (that.props.hide_available ? ' hideAvailable' : '') +
              (that.props.hide_unavailable ? ' hideUnavailable' : '') +
              (that.props.hide_other ? ' hideOther' : '') +
              (that.props.hide_premium ? ' hidePremium' : '')
            }
          >
            {/*
             * Results:
             */}
            <div className="column">
              {suggestions_all.map((phraseString, i) => (
                <div
                  key={i}
                  className={'domname status' + (suggestions_availability[phraseString.replace(/ /g, '')] || 0)}
                >
                  {/*<i className="far fa-heart domname-icon"></i>*/}
                  <span>{phraseString.split(' ').map((str, i) => (
                    <span key={str + i} className="word">{str}</span>
                  ))}</span>
                  &nbsp;&nbsp;
                  <span className="domname-v">v</span>
                </div>
              ))}
            </div>

            {!!that.state.showWip && (
              <>
                {/*
                 * DOMAIN SUGGESTIONS
                 */}
                {Object.keys(suggestions_lists || {}).map((title, i) => {
                  let list = suggestions_lists[title];
                  return (
                    <div key={title + i} className="column">
                      <h3>
                        <b>{title}</b>
                      </h3>
                      <div className="pre">
                        {/* arr_sort_by_width(item.list)*/}
                        {list.map((phraseString, i) => {
                          return (
                            <div
                              key={i}
                              className={'domname status' + suggestions_availability[phraseString.replace(/ /g, '')] || 0}
                            >
                              {phraseString}
                            </div>
                          );
                        })}
                      </div>
                    </div>
                  );
                })}

                {/*
                 * WIP PHRASES (debugging, temporary)
                 */}
                <div className="column">
                  <div className="nowrap">
                    <b>{'wip phrases'}</b>
                  </div>
                  <div className="pre">{suggestions_wip_phrases.map((words, i) => JSON.stringify(words, null, ' '))}</div>
                </div>

                {/*
                 * WIP PHRASE LISTS (debugging, temporary)
                 */}
                {Object.keys(suggestions_wip || {}).map((title, i) => {
                  let lists = suggestions_wip[title];
                  return (
                    <div key={title + i} className="column">
                      <div className="nowrap">
                        <b>{title}</b>
                      </div>
                      <div className="pre">{lists.map((list, i) => JSON.stringify(list, null, ' '))}</div>
                    </div>
                  );
                })}
              </>
            )}


            {/*
             * TLDs
             */}
            <Tlds that={that}/>

          </ColorsStyled>
        </div>
      </DomainsResultsStyled>
    </>
  );
};

export default DomainsResults;
