import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import api_actions from 'src/redux/actions/api';
import { StyledPage, StyledSearch } from '../../../Layout.styled';
import Search from '../../../components/Search';
import WordPoss from '../../../components/WordPoss';
import EditDomainResults from './EditDomainResults';
import { withRouter } from 'react-router-dom';

class EditDomain extends React.Component {
  constructor(props) {
    // etc
    super(props);
    this.state = {
      synsDict: {},
      dom: {},
      all_domains: []
    };
  }

  submit_state_dom = async () => {
    let res = await this.props.api_actions.data_domain_edit(this.state.dom);
    if (res && res.data) {
      this.getData();
    }
  };
  gotData = dom => {
    let syns1 = Array.isArray(dom.syns1) ? dom.syns1 : [];
    // show new data
    this.setState({
      dom: {
        ...dom,
        // convert syns1 to string
        syns1:
          syns1
            .map(w => w.trim())
            .filter(w => !!w)
            .join(', ') + (syns1.length ? ', ' : '')
      }
    });
    // scroll to top
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };
  gotDataDict = dom => {
    let synsDict = {};
    if (dom) {
      for (let key in dom) {
        let list = dom[key];
        let expanded = [];
        let word = '';
        let i = 0;
        for (let entry of list) {
          if (i % 2 === 0) {
            word = entry.toString().replace(/,/g, ', ');
            expanded.push(word);
          }
          if (i % 2 === 1) {
            // word += '\t\t' + entry.toString().replace(/,/g, ', ');
            // expanded.push(word);
            word = '';
          }
          i++;
        }
        synsDict[key] = expanded;
      }
    }
    // show new data
    this.setState({
      synsDict: synsDict
    });
  };
  getData = async () => {
    // get data
    let { tld } = this.props;
    if (tld) {
      // get row (tld, syns1, syns3)
      let dom = await this.props.api_actions.data_domain_get(tld);
      if (dom && dom.key) {
        this.gotData(dom);
      }
      // get expanded list of only syns2
      let data = await this.props.api_actions.data_domain_syns_dict(tld);
      if (data) {
        this.gotDataDict(data);
      }
    }
  };
  getAllDomains = async () => {
    let doms = await this.props.api_actions.data_domains_all();
    this.setState({
      all_domains: doms || []
    });
  };

  componentDidMount() {
    this.getAllDomains();
    if (this.props.tld) {
      this.getData();
    }
  }

  componentDidUpdate(prevProps) {
    let last = prevProps.tld;
    let next = this.props.tld;
    if (next && next !== last) {
      this.getData();
    }
  }

  render() {
    window.doneLoading('render');
    let { tld } = this.props;
    let { dom } = this.state;

    /*
     * Verify content:
     */
    let PageContent = null;
    // nothing queried yet:
    if (!tld) {
      PageContent = <div className="content"></div>;
    }
    // no domain:
    else if (!dom || !dom.key) {
      PageContent = <div className="content">Not found "<b>{tld}</b>"</div>;
    }
    // verified OK:
    else {
      PageContent = <EditDomainResults that={this}/>;
    }

    /*
     * Render content:
     */
    return (
      <>
				<StyledSearch>
					<Search
            {...this.props}
            domains={true}
            hideInput={true}
            cue={[
              <span key="1">&#9757; Choose a domain extension to edit.&nbsp;</span>,
            ]}
          />
					<WordPoss {...this.props} />
				</StyledSearch>

				<StyledPage>{PageContent}</StyledPage>
			</>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    api_actions: bindActionCreators(api_actions, dispatch)
  };
};

const mapStateToProps = function (state) {
  return {
    tld: Object.keys(state.domain.tlds_user)[0],
    tlds_user: state.domain.tlds_user,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(EditDomain));
